@font-face {
  font-family: "Lato";
  src: url("../fonts/lato-400.woff2") format("woff2");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Oswald";
  src: url("../fonts/oswald-400.woff2") format("woff2");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Oswald";
  src: url("../fonts/oswald-500.woff2") format("woff2");
  font-weight: 500;
  font-style: normal;
  font-display: swap;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.page {
  height: 100%;
  margin: 0;
  padding: 0;
}

.page__body {
  height: 100%;
  margin: 0 auto;
  padding: 0;
  font-family: "Lato", "Arial", sans-serif;
  font-size: 14px;
  line-height: 18px;
  font-weight: 400;
  color: #000000;
}

.main-container {
  min-height: 100%;
}

.additionally-product__container {
  max-width: 1220px;
  margin: 0 auto;
  width: calc(100% - 40px);
  padding: 25px 0 35px;
}
@media (min-width: 768px) {
  .additionally-product__container {
    width: calc(100% - 60px);
  }
}
@media (min-width: 1440px) {
  .additionally-product__container {
    width: calc(100% - 220px);
  }
}
@media (min-width: 768px) {
  .additionally-product__container {
    padding: 68px 0 76px;
  }
}
@media (min-width: 1440px) {
  .additionally-product__container {
    padding: 76px 0 85px;
  }
}

@media (min-width: 1440px) {
  .additionally-product__wrapper {
    display: grid;
    grid-template-columns: 1fr 245px;
    column-gap: 80px;
  }
}

.additionally-product__title {
  margin: 0;
  margin-bottom: 35px;
  font-size: 24px;
  line-height: 24px;
  position: relative;
}
@media (min-width: 768px) {
  .additionally-product__title {
    font-size: 36px;
    line-height: 36px;
    margin-bottom: 80px;
  }
}
.additionally-product__title span {
  display: block;
  width: fit-content;
  background-color: #ffffff;
  padding: 0 21px 0 20px;
  margin-left: -20px;
}
@media (min-width: 768px) {
  .additionally-product__title span {
    padding: 0 36px 0 37px;
    margin-left: 0;
  }
}
@media (min-width: 1440px) {
  .additionally-product__title span {
    padding: 0 35px 0 40px;
    margin-left: -40px;
  }
}
.additionally-product__title::after {
  content: "";
  position: absolute;
  height: 2px;
  width: 100vw;
  background-color: #68b738;
  top: 60%;
  z-index: -1;
  left: calc((100vw - 100%) / -2);
}

.additionally-product__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  margin-bottom: 34px;
}
@media (min-width: 768px) {
  .additionally-product__list {
    margin-bottom: 68px;
  }
}
@media (min-width: 1440px) {
  .additionally-product__list {
    margin-bottom: 0;
  }
}

.arguments {
  font-family: "Oswald", "Arial", sans-serif;
  text-align: center;
  text-transform: uppercase;
}

.arguments__wrapper {
  margin-bottom: 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
  row-gap: 34px;
}
@media (min-width: 768px) and (max-width: 1439px) {
  .arguments__wrapper {
    margin-bottom: 104px;
    flex-direction: row;
    justify-content: space-between;
  }
}
@media (min-width: 1440px) {
  .arguments__wrapper {
    row-gap: 60px;
    margin: 0;
  }
}

.arguments__list {
  margin: 0;
  width: 280px;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  column-gap: 32px;
}
@media (min-width: 768px) {
  .arguments__list {
    width: 366px;
    column-gap: 42px;
  }
}

.arguments__item {
  padding: 13px 0 17px;
  position: relative;
  border: 1px solid #cdcdcd;
  border-radius: 3px;
  box-sizing: border-box;
}
@media (min-width: 768px) {
  .arguments__item {
    padding: 14px 0 19px;
  }
}

.arguments__value {
  font-size: 24px;
  line-height: 24px;
}
@media (min-width: 768px) {
  .arguments__value {
    font-size: 30px;
    line-height: 37px;
  }
}

.arguments__description {
  margin: 0;
  padding: 0 14px;
  font-family: "Lato", "Arial", sans-serif;
  font-size: 12px;
  line-height: 12px;
  text-transform: lowercase;
  width: 87px;
  box-sizing: border-box;
  position: absolute;
  top: 46px;
  left: 50%;
  transform: translateX(-50%);
  background-color: #eaeaea;
}
@media (min-width: 768px) and (max-width: 1439px) {
  .arguments__description {
    background-color: #ffffff;
  }
}
@media (min-width: 768px) {
  .arguments__description {
    width: max-content;
    padding: 0 9px;
    font-size: 14px;
    line-height: 14px;
    top: 63px;
  }
}

.arguments__resume {
  margin: 0;
  display: flex;
  line-height: 20px;
}
@media (min-width: 768px) and (max-width: 1439px) {
  .arguments__resume {
    flex-direction: column;
    align-items: flex-end;
    row-gap: 9px;
  }
}
@media (min-width: 768px) {
  .arguments__resume {
    font-size: 20px;
    line-height: 20px;
  }
}
@media (min-width: 1440px) {
  .arguments__resume {
    align-self: flex-start;
    column-gap: 63px;
  }
}

.button {
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 16px;
  line-height: 20px;
  text-align: center;
  text-transform: uppercase;
  text-decoration: none;
  color: #ffffff;
  border: none;
}
.button--green {
  background-color: #68b738;
}
.button--green:hover {
