}
.button--gray {
  background-color: #f2f2f2;
  color: #444444;
}
.button--gray:hover {
  background-color: #ebebeb;
}
.button--gray:active {
  background-color: #ebebeb;
  color: rgba(68, 68, 68, 0.3);
}

.catalog__container {
  max-width: 1220px;
  margin: 0 auto;
  width: calc(100% - 40px);
  display: grid;
}
@media (min-width: 768px) {
  .catalog__container {
    width: calc(100% - 60px);
  }
}
@media (min-width: 1440px) {
  .catalog__container {
    width: calc(100% - 220px);
  }
}
@media (max-width: 767px) {
  .catalog__container {
    position: relative;
  }
  .catalog__container::before {
    content: "";
    position: absolute;
    width: 100vw;
    height: 1px;
    top: 0;
    left: -20px;
    background-color: #ebebeb;
  }
}

.catalog__title {
  font-family: "Oswald", "Arial", sans-serif;
  font-weight: 400;
  margin: 28px 0 41px;
  font-size: 36px;
  line-height: 36px;
}
@media (min-width: 768px) {
  .catalog__title {
    margin: 64px 0 82px;
    font-size: 60px;
    line-height: 60px;
  }
}
@media (min-width: 1440px) {
  .catalog__title {
    margin: 76px 0 84px;
  }
}

.catalog__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
  column-gap: 20px;
}
@media (min-width: 768px) {
  .catalog__list {
    grid-template-columns: repeat(auto-fit, minmax(324px, 1fr));
    gap: 60px;
  }
}
@media (min-width: 1440px) {
  .catalog__list {
    grid-template-columns: repeat(auto-fit, minmax(245px, 1fr));
    gap: 62px 80px;
  }
}

.compare-slider__container {
  display: flex;
  justify-content: center;
  position: relative;
  width: 280px;
  height: 256px;
}
@media (min-width: 768px) {
  .compare-slider__container {
    width: 560px;
    height: 512px;
  }
}

.compare-slider__wrapper {
  width: 50%;
  height: 100%;
  overflow: hidden;
  position: relative;
}

.compare-slider__image {
  position: absolute;
  top: 0;
}
@media (min-width: 768px) {
  .compare-slider__image {
    width: 560px;
    height: auto;
  }
}
.compare-slider__image--before {
  left: 0;
}
.compare-slider__image--after {
  right: 0;
}

.compare-slider__control {
  position: absolute;
  width: 4px;
  height: 100%;
  z-index: 1;
  left: 50%;
  transform: translateX(-50%);
  cursor: pointer;
  box-sizing: border-box;
}
.compare-slider__control .compare-slider__thumb {
  width: 40px;
  height: 40px;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  position: absolute;
  background-color: #ffffff;
  border: 2px solid #666666;
  border-radius: 50%;
  box-sizing: border-box;
  z-index: 2;
}
.compare-slider__control::before {
  content: "";
  position: absolute;
  width: 24px;
  height: 18px;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 3;
  background-color: #666666;
  mask-image: url("../img/icon/sprite.svg#triangles");
}
.compare-slider__control::after {
  content: "";
  width: 4px;
  height: 100%;
  left: 50%;
  transform: translateX(-50%);
  position: absolute;
  background-color: #666666;
  border-radius: 2px;
  z-index: 1;
  box-sizing: border-box;
}
.compare-slider__control:hover::before {
  background-color: #68b738;
}
.compare-slider__control:hover::after {
  background-color: #68b738;
}
.compare-slider__control:hover .compare-slider__thumb {
  border-color: #68b738;
}
.compare-slider__control:active::before {
  background-color: #5eaa2f;
  opacity: 0.6;
}
.compare-slider__control:active::after {
  background-color: #5eaa2f;
  opacity: 0.6;
}
.compare-slider__control:active .compare-slider__thumb {
  border-color: #5eaa2f;
  opacity: 0.6;
}

