.copyright {
  font-size: 16px;
  color: #444444;
  text-decoration: none;
  padding: 9px 0 7px;
  position: relative;
}
.copyright::after {
  content: "";
  width: 27px;
  height: 34px;
  mask-image: url("../img/icon/sprite.svg#html-academy");
  background-color: #666666;
  position: absolute;
  right: 0;
  top: 50%;
  transform: translateY(-50%);
}
.copyright:hover, .copyright:focus {
  outline: none;
}
.copyright:hover::after, .copyright:focus::after {
  background-color: #68b738;
}
.copyright:active {
  color: rgba(68, 68, 68, 0.3);
}
.copyright:active::after {
