}

.field {
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 16px;
  line-height: 18px;
  text-transform: uppercase;
  color: #444444;
  display: flex;
  column-gap: 14px;
  align-items: center;
  min-width: 280px;
  position: relative;
}
@media (min-width: 768px) {
  .field {
    font-size: 20px;
    line-height: 30px;
  }
}

.field__label {
  min-width: max-content;
  flex-grow: 0;
  cursor: pointer;
}

.field__input {
  font-family: inherit;
  font-size: inherit;
  background-color: transparent;
  background-image: none;
  box-shadow: none;
  border: none;
  border-radius: 0;
  margin: 0;
  padding: 0;
  appearance: none;
  outline: 1px solid #e7e7e7;
  padding: 14px 50px 13px 20px;
  box-sizing: border-box;
  flex-grow: 1;
  min-width: 70px;
  line-height: inherit;
  color: inherit;
  text-transform: inherit;
}
@media (min-width: 768px) {
  .field__input {
    padding: 11px 52px 11px 20px;
  }
}
@media (max-width: 767px) {
  .field__input--phone, .field__input--email {
    padding-left: 14px;
  }
}
@media (min-width: 768px) {
  .field__input--phone, .field__input--email {
    margin-left: 7px;
  }
}
.field__input[type=number] {
  appearance: textfield;
}
.field__input::placeholder {
  color: inherit;
}
.field__input:hover {
  outline-color: #cdcdcd;
}
.field__input:focus {
  outline: 2px solid #444444;
}
.field__input:focus + .field__icon {
  background-color: #68b738;
}
.field__input:not(:placeholder-shown):invalid {
  outline: 2px solid #ff8282;
}
.field__input:not(:placeholder-shown):invalid + .field__icon {
  background-color: #ff8282;
}

.field__icon {
  position: absolute;
  width: 20px;
  height: 20px;
  right: 15px;
  background-color: #222222;
}
@media (min-width: 768px) {
  .field__icon {
    right: 18px;
  }
}
.field__icon--email {
  mask-image: url("../img/icon/sprite.svg#email");
}
.field__icon--phone {
  mask-image: url("../img/icon/sprite.svg#phone");
}

.field__textarea {
  width: 100%;
  min-height: 150px;
  resize: none;
  border: 1px solid #e7e7e7;
  font-family: inherit;
  font-size: inherit;
  line-height: inherit;
  color: inherit;
  text-transform: inherit;
  padding: 10px 12px 10px 15px;
  box-sizing: border-box;
}
@media (min-width: 768px) {
  .field__textarea {
    min-height: 174px;
    padding: 10px 19px;
  }
}
.field__textarea:hover {
  border-color: #cdcdcd;
}
.field__textarea:focus {
  outline: 2px solid #444444;
}
.field__textarea:not(:placeholder-shown):invalid {
  outline: 2px solid #ff8282;
}

